<template>
  <div>
    <a-modal
      v-model="dialogShowBookingMeeting"
      centered
      title="ข้อมูลการจองห้องประชุม"
    >
      <a-spin :spinning="spinning">
        <div>
       <a-empty v-if="this.empty_data == true" :description="false" />
          <v-row
            class="pt-5"
            align="center"
            justify="center"
            v-for="items in this.data_userBooking"
            :key="items"
          >
            <v-col class="pa-0" v-if="items.guest == false">
     
              <a-card>
                <a-timeline>
                  <a-timeline-item color="green">
                    <a-icon
                      class="pr-5"
                      slot="dot"
                      type="solution"
                      style="font-size: 18px;"
                    />
                    {{ "หัวข้อประชุม : " + items.agenda }}
                  </a-timeline-item>
                  <a-timeline-item color="red">
                    <a-icon
                      slot="dot"
                      type="calendar"
                      style="font-size: 18px;"
                    />
                    {{ parseToDateFormat(items.meeting_start) }}
                  </a-timeline-item>

                  <a-timeline-item color="pink">
                    <a-icon slot="dot" type="bank" style="font-size: 18px;" />
                    {{ "ห้องประชุมที่ : " + items.room_num }}
                  </a-timeline-item>
                  <a-timeline-item color="blue">
                    <a-icon
                      slot="dot"
                      type="clock-circle-o"
                      style="font-size: 18px;"
                    />
                    {{
                      parseToTimeFormat(items.meeting_start, items.meeting_end)
                    }}
                  </a-timeline-item>

                  <a-timeline-item color="green">
                    <a-icon slot="dot" type="user" style="font-size: 18px;" />
                    {{ "ชื่อผู้จอง : " + items.one_email }}
                  </a-timeline-item>

                  <a-timeline-item color="red">
                    <a-icon slot="dot" type="team" style="font-size: 18px;" />
                    <p>ผู้ร่วมประชุม :</p>
                    <div v-for="item_tag in items.guest_email" :key="item_tag">
                      <a-tag class="mt-2" color="blue">
                        {{ item_tag }}
                      </a-tag>
                    </div>
                  </a-timeline-item>
                </a-timeline>
                <v-row align="center" justify="center">
                  <v-col align="center">
                    <a-button
                      type="danger"
                      size="large"
                      @click="OpenModal_before_eject(items)"
                    >
                      ยกเลิกการจอง
                    </a-button>
                  </v-col>
                </v-row>
              </a-card>
            </v-col>
          </v-row>
        </div>
      </a-spin>

      <template slot="footer">
        <a-button
          key="back"
          type="primary"
          @click="close_dialogShowBookingMeeting()"
        >
          ปิด
        </a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
export default {
  components: {},
  data: () => ({
    dialogShowBookingMeeting: false,
    user_login: "",
    data_userBooking: null,
    spinning: false,
    modal_eject: null,
    empty_data: false,
    email_user:""
  }),
  created() {
    this.getdata_storage();
  },
  methods: {
    open() {
      this.dialogShowBookingMeeting = true;
      this.API_GetBookingCalendar_UserFuture();
    },
    close_dialogShowBookingMeeting() {
      this.dialogShowBookingMeeting = false;
    },
    cleanSpinning() {
      this.spinning = false;
    },
    API_GetBookingCalendar_UserFuture() {
      this.empty_data = false
      this.spinning = true;

      this.axios({
        method: "GET",
        url:
          this.$store.state.url_get_API_axios +
          "api/v1/bookingTable/" +
          this.email_user +
          "/future",
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          console.log(response.data);
          this.cleanSpinning();

          if (response.data != null) {
            this.data_userBooking = response.data;
            this.empty_data = false

          } else if (response.data == []) {
            this.empty_data = true;
          }
        })
        .catch((error) => {
          this.cleanSpinning();
          console.log(error.message);
          // this.loading = false;
          if (error.message == "Request failed with status code 400") {
            this.$message.error("ไม่พบข้อมูล");
            this.cleanSpinning();
          } else if (error.message == "Network Error") {
            this.$error({
              centered: true,
              title: "ไม่สามารถค้นหาห้องประชุมได้",
              content: "กรุณาตรวจสอบเครือข่าย !!!",
            });
            this.cleanSpinning();
          }
        });
    },
    OpenModal_before_eject(data) {
      this.modal_eject = this.$confirm({
        title: "ยืนยันการยกเลิกหรือไม่?",
        content: "หากท่านต้องการยกเลิกการจอง กดปุ่มยืนยัน",
        okText: "ยืนยัน",
        okType: "danger",
        centered: true,
        cancelText: "ยกเลิก",

        onOk: () => {
          this.$emit(this.API_RejectBooking(data));
        },
        onCancel() {
          console.log("Cancel");
        },
      });
    },
    API_RejectBooking(data) {
      this.empty_data = false
      this.modal_eject.destroy();
      this.spinning = true;
      this.axios({
        method: "GET",
        url:
          this.$store.state.url_get_API_axios +
          "api/v1/ejectBooking/" +
          this.email_user +
          "/" +
          data.booking_number,
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          console.log(response.data);
          var results = response.data;
          if (results.Status == "Eject Successful") {
            this.API_GetBookingCalendar_UserFuture();
          } else if (
            results.Status == "Booking Number or One Email Can Not be Null"
          ) {
              this.cleanSpinning();
          }
          this.cleanSpinning();
        })
        .catch((error) => {
          console.log(error.response);
          if (error.message == "Request failed with status code 400") {
            this.$message.error("ไม่พบข้อมูล");
            this.cleanSpinning();
          }
          this.cleanSpinning();
        });
    },
    parseToDateFormat(dateString) {
      let date = new Date(dateString);

      return (
        "วันที่ : " +
        date.getDate() +
        " - " +
        (date.getMonth() + 1) +
        " - " +
        date.getFullYear()
      );
    },
    parseToTimeFormat(startTime, endTime) {
      let timeStart = new Date(startTime);
      let timeEnd = new Date(endTime);
      var timeStart_min = 0;
      var timeEnd_min = 0;
      if (timeStart.getMinutes() == 0) {
        timeStart_min = timeStart.getMinutes() + "0";
      } else {
        timeStart_min = timeStart.getMinutes();
      }
      if (timeEnd.getMinutes() == 0) {
        timeEnd_min = timeEnd.getMinutes() + "0";
      } else {
        timeEnd_min = timeEnd.getMinutes();
      }
      return (
        "เวลา : " +
        timeStart.getHours() +
        ":" +
        timeStart_min +
        " - " +
        timeEnd.getHours() +
        ":" +
        timeEnd_min +
        " น."
      );
    },
    getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("user_data"));

      if (data === null) {
        this.getdata = null;
      } else {
        this.email_user = data.email;
      }
    },
  },
};
</script>
<style scoped></style>
