<template>
  <div>
    <a-card style="">
      <v-row align="center" justify="center">
        <v-col class="mt-3 pt-0 pb-0" cols="7" align="left">
          <p>ปฏิทินการจองห้องประชุม</p>
        </v-col>

        <v-col class="mb-1 pt-0 pb-0" cols="5" align="right">
          <a-icon class="pl-2" type="calendar" />
        </v-col>
      </v-row>
    </a-card>

    <v-row class="mt-1" align="center" justify="center">
      <v-col align="center">
        <div style="border: 1px solid #d9d9d9; border-radius: 4px">
          <a-calendar
            :fullscreen="false"
            :header-render="headerRender"
            :disabled-date="disabledDate"
            @select="onSelect"
            format="DD-MM-YYYY"
            @panelChange="onPanelChange"
          >
            <ul slot="dateCellRender" slot-scope="value" class="events">
              <li v-for="item in getListData(value)" :key="item.content">
                <a-badge :status="item.type" :text="item.content" />
              </li>
            </ul>
            <template slot="monthCellRender" slot-scope="value">
              <div v-if="getMonthData(value)" class="notes-month">
                <section>{{ getMonthData(value) }}</section>
                <span>Backlog number</span>
              </div>
            </template>
          </a-calendar>
        </div>
      </v-col>
    </v-row>

    <a-card class="mt-4">
      <v-row align="" justify="center">
        <v-col cols="6" class="pa-0" align="center">
          <v-card-text class="pa-0 font-weight-bold"
            >กดปุ่มดูการจองของท่าน</v-card-text
          >

          <a-button
            class="mt-6"
            type="primary"
            size="large"
            @click="OpenDialogShowBookingMeeting()"
          >
            <a-icon type="schedule" />
            ดูการจอง
          </a-button>
        </v-col>
        <v-divider vertical inset></v-divider>

        <v-col cols="5" class="pa-0" align="right">
          <v-card-text class="pa-0 font-weight-bold">สถานะการจอง</v-card-text>
          <v-card-text class="pl-0 pb-0 pr-0">
            <v-icon class="mr-2" color="red">mdi-alert-circle</v-icon>
            หมายถึง เต็ม</v-card-text
          >
          <v-card-text class="pb-0 pr-0">
            <v-icon class="mr-2" color="green">mdi-alert-circle</v-icon>
            หมายถึง ว่าง</v-card-text
          >
        </v-col>
      </v-row>
    </a-card>
    <DialogSearchRoom ref="DialogSearchRoom"></DialogSearchRoom>
    <DialogAddMeeting ref="DialogAddMeeting"></DialogAddMeeting>
    <DialogShowBookingMeeting
      ref="DialogShowBookingMeeting"
    ></DialogShowBookingMeeting>
  </div>
</template>
<script>
import DialogSearchRoom from "./DialogSearchRoom.vue";
import DialogAddMeeting from "./DialogAddMeeting.vue";
import DialogShowBookingMeeting from "./DialogShowBookingMeeting.vue";
import moment from "moment";

export default {
  components: {
    DialogSearchRoom,
    DialogAddMeeting,
    DialogShowBookingMeeting,
  },
  data: () => ({
    List_booking_all: null,
    day_event_calendar: [3, 5],
  }),
  created() {
    // this.API_GetBookingCalendar();
  },
  methods: {
    moment,
    onSelect(value) {
      if (value != null) {
        var myJSONselectedValue = JSON.stringify(value);
        this.OpenDialogSearchRoom(myJSONselectedValue);
      } else {
        this.$message.error("กรุุณาเลือกวันที่ให้ถูกต้อง");
      }
    },
    onPanelChange(value, mode) {
      console.log(value, mode);
    },
    disabledDate(current) {
      return current && current < moment().startOf("day");
    },
  
    OpenDialogSearchRoom(myJSONselectedValue) {
      var selectedValue = myJSONselectedValue.slice(1, 11);
      this.$refs.DialogSearchRoom.open(selectedValue);
    },
    OpenDialogShowBookingMeeting() {
      this.$refs.DialogShowBookingMeeting.open();
    },
    getListData(value) {
      let listData;
      console.log(listData);

      switch (value.date()) {
        case 8:
          listData = [
            { type: "warning", content: "This is warning event." },
            { type: "success", content: "This is usual event." },
          ];
          break;
        case 10:
          listData = [
            { type: "warning", content: "This is warning event." },
            { type: "success", content: "This is usual event." },
            { type: "error", content: "This is error event." },
          ];
          break;
        case 15:
          listData = [
            { type: "warning", content: "This is warning event" },
            {
              type: "success",
              content: "This is very long usual event。。....",
            },
            { type: "error", content: "This is error event 1." },
            { type: "error", content: "This is error event 2." },
            { type: "error", content: "This is error event 3." },
            { type: "error", content: "This is error event 4." },
          ];
          break;
        default:
      }
    },

    getMonthData(value) {
      if (value.month() === 8) {
        return 1394;
      }
    },
    API_GetBookingCalendar() {
      this.axios({
        method: "GET",
        url: this.$store.state.url_get_API_axios + "api/v1/bookingTable",
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          // console.log(response.statusText);
          // console.log(response.data);
          this.results = response.data;

          if (response.statusText == "OK") {
            this.List_booking_all = this.results;
             console.log(this.List_booking_all);
          } else {
            // this.spinning = false;

            this.$error({
              title: "ไม่พบข้อมูล",
              content: "ไม่พบข้อมูล",
            });
          }
        })
        .catch((error) => {
          // this.spinning = false;
          console.log(error.message);
          // this.loading = false;
          // if (error.message == "Request failed with status code 400") {
          //   this.$message.error("ไม่พบข้อมูล");
          // }
        });
    },
     headerRender({ value, type, onChange, onTypeChange  }) {
      const Now_Month = new Date(value);
      const Next_Month = new Date(value);

      const start = Now_Month.getMonth();
      const end = (Next_Month.getMonth() + 1);
      const end_month = end;
      const monthOptions = [];

      const current = value.clone();
      const localeData = value.localeData();
      const months = [];
      for (let i = 0; i < 12; i++) {
        current.month(i);
        months.push(localeData.monthsShort(current));
      }
      console.log(end_month);
      for (let index = start; index <= end; index++) {
        console.log(index);
        console.log(end);
        if(index <= end_month){
           monthOptions.push(
          <a-select-option class="month-item" key={`${index}`}>
            {months[index]}
          </a-select-option>,
        );
        }
       
      }
      const month = value.month();

   
      return (
             <div style={{ padding: '10px' }}>

          <a-row type="flex" justify="space-between">
     <a-col>
              <a-radio-group size="small" onChange={e => onTypeChange(e.target.value)} value={type}>
                <a-radio-button value="month">Month</a-radio-button>
              </a-radio-group>
            </a-col>
            <a-col>
              <a-select
                
                dropdownMatchSelectWidth={false}
                value={String(month)}
                onChange={selectedMonth => {
                  const newValue = value.clone();
                  newValue.month(parseInt(selectedMonth, 10));
                  onChange(newValue);
                }}
              >
                {monthOptions}
              </a-select>
            </a-col>
          </a-row>
        </div>
      );
    },
  },
};
</script>
<style scoped>
.events {
  list-style: none;
  margin: 0;
  padding: 0;
}
.events .ant-badge-status {
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  text-overflow: ellipsis;
  font-size: 12px;
}
.notes-month {
  text-align: center;
  font-size: 28px;
}
.notes-month section {
  font-size: 28px;
}
</style>
