<template>
  <div>
    <a-modal v-model="dialog" centered title="ค้นหาห้องประชุม">
      <a-spin :spinning="spinning">
        <div class="spin-content">
          <v-row class="pt-5" align="center" justify="center">
            <v-col>
              <a-alert
                :message="this.message_alert"
                description="โปรดกดเลือกเวลาด้านล่าง เพื่อค้นหาห้องประชุมที่ท่านต้องการ"
              />
            </v-col> </v-row
          ><br /><br />

          <v-row class="" align="center" justify="center">
            <v-col class="" align="center" style="padding-bottom: 0px;">
              <a-form>
                <a-form-item label="เวลาเริ่มต้น">
                  <a-time-picker
                    inputReadOnly
                    :minute-step="1"
                    :disabledHours="disabledHours"
                    :disabledMinutes="disabledMinutes"
                    @change="onChange_Start_Time"
                    style="width: 100%"
                    :value="value_starttime"
                    format="HH:mm"
                  />
                </a-form-item>
              </a-form>
            </v-col>

            <v-col class="" align="center" style="padding-bottom: 0px;">
              <a-form>
                <a-form-item label="เวลาสิ้นสุด">
                  <div name="disabled-keyboard--input">
                    <a-time-picker
                      inputReadOnly
                      :minute-step="1"
                      :disabledHours="disabledHours"
                      :disabledMinutes="disabledMinutes"
                      @change="onChange_End_Time"
                      style="width: 100%"
                      :value="value_endtime"
                      format="HH:mm"
                    />
                  </div>
                </a-form-item>
              </a-form>
            </v-col>
          </v-row>
        </div>
      </a-spin>
      <template slot="footer">
        <a-button key="back" @click="handleCancel">
          ปิด
        </a-button>
        <a-button key="submit" type="primary" @click="API_search_MeetingRoom()">
          ค้นหา
        </a-button>
      </template>
    </a-modal>

    <DialogAddMeeting ref="DialogAddMeeting"></DialogAddMeeting>
  </div>
</template>
<script>
import DialogAddMeeting from "./DialogAddMeeting.vue";
import moment from "moment";
export default {
  components: {
    DialogAddMeeting,
  },
  data: () => ({
    dialog: false,
    select_to_date: null,
    value_starttime: null,
    value_endtime: null,
    start_time: null,
    end_time: null,
    delayTime: 500,
    spinning: false,
    date_not_convert_select: null,
    date_convert_select: "",
    loading: false,
    message_alert: "",
    source_api: {},
    data_value_room: null,
  }),
  methods: {
    moment,
    open(data) {
      this.date_not_convert_select = data;
      var dd = data.slice(8, 10);
      var mm = data.slice(5, 7);
      var yy = data.slice(0, 4);
      this.date_convert_select = dd + "-" + mm + "-" + yy;
      this.message_alert = "วันที่ท่านเลือก : " + this.date_convert_select;
      this.dialog = true;
    },
    onChange_Select_Date(date, dateString) {
      if (dateString != null) {
        this.select_to_date = dateString;
      }
    },
    onChange_Start_Time(time, timeString) {
      this.value_starttime = time;
      this.start_time = timeString;
    },
    onChange_End_Time(time, timeString) {
      this.value_endtime = time;
      this.end_time = timeString;
    },
    disabledHours() {
      var hours = [];
      for (let i = 0; i < moment().hour(); i++) {
        hours.push(i);
      }
      return hours;
    },
    disabledMinutes(selectedHour) {
      var minutes = [];
      if (selectedHour === moment().hour()) {
        for (var i = 0; i < moment().minute(); i++) {
          minutes.push(i+1);
        }
      }
      return minutes;
    },
    API_search_MeetingRoom() {
      // console.log(this.date_not_convert_select);
      // console.log(this.start_time);
      // console.log(this.end_time);
      this.spinning = true;
      if (this.start_time == null || this.end_time == null) {
        this.$message.error("กรุุณาเลือกข้อมูลให้ครบถ้วน");
      } else {
        this.source_api = {
          meeting_start: this.date_not_convert_select + " " + this.start_time,
          meeting_end: this.date_not_convert_select + " " + this.end_time,
        };
        // console.log(this.source_api);
        // console.log(this.$store.state.url_get_API_axios);
        this.axios({
          method: "POST",
          url:
            this.$store.state.url_get_API_axios + "api/v1/checkAvailableRoom",
          data: this.source_api,
        })

          .then((response) => {
            // console.log(response);
            this.cleanSpinning()
            if (response.data.Status == "These Rooms are Available") {
              this.data_value_room = response.data.value;
              this.cleanSpinning()
              this.Open_DialogAdd_event_Meeting();
            } else {
              this.Model_countDown_error();
            }
          })
          .catch((error) => {
            this.cleanSpinning()
            console.log(error.response.data);
            console.log(error.message);
            if (error.response.data.Status == "Datetime is Invalid") {
              this.cleanSpinning()
              this.Model_countDown_error();
            } else if (
              error.response.data.Status ===
              "Start Time Should Be Less Then End Time"
            ) {
              this.cleanSpinning()
              this.Model_countDown_error();
            }
            else if(error.message == "Network Error"){
              this.cleanSpinning();
               this.$error({
                    centered: true,
                    title: 'ไม่สามารถค้นหาห้องประชุมได้',
                    content: 'กรุณาตรวจสอบเครือข่าย !!!',
                    
                  });
            }
          });
      }
    },
    Model_countDown_error() {
      let secondsToGo = 3;
      const modal = this.$error({
        title: "ไม่สามารถค้นหาห้องประชุมได้",
        content: `โปรดเลือกเวลาประชุมอีกครั้ง`,
        centered: true,
      });
      const interval = setInterval(() => {
        secondsToGo -= 1;
        modal.update({
          content: `โปรดเลือกเวลาประชุมอีกครั้ง`,
        });
      }, 1000);
      setTimeout(() => {
        clearInterval(interval);
        modal.destroy();
      }, secondsToGo * 1000);
    },
    cleanSpinning() {
      this.spinning = false;
    },
    Open_DialogAdd_event_Meeting() {
      this.dialog = false;
      // console.log(this.date_not_convert_select);
      // console.log(this.date_convert_select);
      // console.log(this.start_time);
      // console.log(this.end_time);
      // console.log(this.data_value_room);
      if (this.data_value_room != null) {
        var data = {
          date_not_convert_select: this.date_not_convert_select,
          date_convert_select: this.date_convert_select,
          start_time: this.start_time,
          end_time: this.end_time,
          room_source_api: this.data_value_room,
        };

        this.$refs.DialogAddMeeting.open(data);
      } else {
        this.Model_countDown_error();
      }
    },
    handleCancel(e) {
      console.log(e);
      this.dialog = false;
    },
  },
};
</script>
<style scoped></style>
